import { Component} from '@angular/core';
import {ES, ENG} from './strings'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  ngOnInit() {
  }

  lang = ES;
  langs = [ES, ENG];

  onSelect(l): void {
    this.lang = l;
  }

  scroll(element) {
    element.scrollIntoView({behavior:"smooth",block: "start"});
  } 
}
